調整高度比例\r\n  position: relative; // 父容器相對定位\r\n}\r\n\r\n.left-panel, .right-panel {\r\n  background-color: $panel-bg;\r\n  padding: 20px;\r\n  border-radius: 10px;\r\n  width: 50%; // 調整寬度比例\r\n  height: 100%; // 填滿父容器的高度\r\n\r\n  h2 {\r\n    margin-bottom: 20px;\r\n    font-size: 2rem; // 調整標題字體大小\r\n  }\r\n}\r\n\r\n.home-btn {\r\n  font-size: 16px;\r\n  color: #0066cc;\r\n  text-decoration: none;\r\n  font-weight: bold;\r\n  padding: 5px 10px;\r\n  border: 2px solid #0066cc;\r\n  border-radius: 5px;\r\n  transition: background-color 0.3s, color 0.3s;\r\n  margin: 40px;\r\n}\r\n.home-btn:hover {\r\n  background-color: #0066cc;\r\n  color: white;\r\n}\r\n\r\n// 返回按鈕樣式\r\n.back-button {\r\n  position: absolute; // 使用絕對定位\r\n  bottom: 0px; // 向下移動距離\r\n  left: 30px; // 向右移動距離\r\n  background-color: $gray; // 按鈕背景顏色\r\n  color: $black; // 按鈕文字顏色\r\n  padding: 5px 15px; // 內邊距\r\n  border-radius: 5px; // 圓角\r\n  border: none; // 去除邊框\r\n  cursor: pointer; // 游標效果\r\n  font-size: 0.9rem; // 字體大小\r\n  display: inline-block; // 行內塊顯示模式\r\n  text-align: center; // 文本置中\r\n  transition: background-color 0.3s ease; // 加入滑順的背景顏色變化效果\r\n\r\n  &:hover {\r\n    background-color: #000000;// 滑鼠懸停時背景顏色\r\n    color: #ffffff; // 滑鼠懸停時文字顏色\r\n  }\r\n}\r\n\r\n// 左側資產概覽\r\n.asset-summary {\r\n  p {\r\n    margin: 10px 0;\r\n\r\n    .amount {\r\n      font-weight: bold;\r\n\r\n      &.green {\r\n        color: $red;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n// 右側資產分佈\r\n.chart-container {\r\n  display: flex;\r\n  align-items: center; // 保持垂直置中\r\n  gap: 40px; // 增加圖表與圖例之間的間距\r\n\r\n  .pie-chart {\r\n    width: 150px; // 調整圓形圖的大小\r\n    height: 150px;\r\n    background: conic-gradient(\r\n      $orange 0% 64.7%,\r\n      $red 64.7% 76.9%,\r\n      $pink 76.9% 89%,\r\n      $purple 89% 96%,\r\n      $yellow 96% 100%\r\n    );\r\n    border-radius: 50%;\r\n  }\r\n\r\n  .chart-legend {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center; // 保持垂直置中\r\n    font-size: 1.2rem; // 調整圖例文字大小\r\n\r\n    ul {\r\n      list-style: none;\r\n      padding: 0;\r\n      margin: 0;\r\n\r\n      li {\r\n        margin: 10px 0; // 增加每行之間的間距\r\n        display: flex;\r\n        align-items: center;\r\n\r\n        .color-box {\r\n          width: 15px;\r\n          height: 15px;\r\n          display: inline-block;\r\n          margin-right: 10px;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n\r\n\r\n\r\n\r\n"],"sourceRoot":""}]);
